<template>
    <div class="clearfix h-full">
        <div class="panel-nav">
            <el-breadcrumb separator="/" class="m-l-lg">
                <el-breadcrumb-item>
                {{$t('myClass.title')}}
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="panel-body-list clearfix">
            <list-table
                :data="listData"
                :fields="fields"
                :operators="operators"
                class="wrapper-md"
                :operatorsTitle="$t('myClass.operate')"
                :isSearchLeft="false">
                <div slot="btns-left">
                    <button class="btn btn-transform padder" @click="removeItems">{{$t('myClass.remove')}}</button>
                </div>
                <div slot="btns">
                    <button class="fa fa-refresh btn-refresh" @click="refresh"></button>
                </div>
            </list-table>
        </div>
    </div>
</template>
<script>
    import listTable from '../table/table'

    export default {
        created() {
            this.getIsMonitor()
        },
        data() {
            return {
                listData: [],
                isMonitor: false,
                fields: [{
                    name: 'username',
                    showName: this.$t('myClass.username')
                }, {
                    name: 'name',
                    showName: this.$t('myClass.name')
                }, {
                    name: 'job',
                    showName: this.$t('myClass.job')
                }, {
                    name: 'cellphone',
                    showName: this.$t('myClass.cellphone')
                }, {
                    name: 'email',
                    showName: this.$t('myClass.email')
                }],
                operators: []
            }
        },
        methods: {
            getIsMonitor() {
                if(false) {
                    return
                }
                let operate = [{
                    name: this.$t('myClass.operate'),
                    handler: this.remove
                }]
                this.operators.push(operate)
            },
            refresh() {

            },
            remove() {

            },
            removeItems() {
                
            }
        },
        components: {
            listTable
        }
    }
</script>
<i18n>
    {
        "zh": {
            "myClass": {
                "title": "我的班级",
                "remove": "移除",
                "username": "用户名",
                "name": "学生姓名",
                "job": "职务",
                "operate": "操作",
                "cellphone": "手机号码",
                "email": "电子邮箱"
            }
        }
    }
</i18n>
<style>
    
</style>


